.search-box {
  position: relative;
  height: 40px;
}

.search-box .search-input {
  width: 100%;
  height: 100%;
  padding-right: 40px;
}

.search-box button {
  position: absolute;
  inset: 0 0 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  aspect-ratio: 1 / 1;
  background: #ffffff;
  color: #001f3f;
  border: 1px solid #ccc;      /* ← this is the new border */
  border-radius: 4px;          /* ← optional: gives it a little roundness */
  cursor: pointer;
}
.search-input {
  background-color: #ffffff !important; /* Sets background to white */
  color: #001f3f !important;            /* Sets text color to black */
}
